<script>
import wx from 'weixin-js-sdk'

export default {
  data (){
    return{
      username:'土豆',
      vipLevel:3
    }
  },
  methods:{
    
  },
  computed:{
    levelClass:function () {
      switch (this.vipLevel) {
        case 0:
          return "vip"
          break;
        case 1:
          return "diamond"
          break;
        case 2:
          return "crown"
          break;
        case 3:
          return "top"
          break;      
        default:
          return "vip"
          break;
      }
    },
    levelTitle:function(){
    	switch (this.vipLevel) {
        case 0:
          return "VIP会员"
          break;
        case 1:
          return "钻石会员"
          break;
        case 2:
          return "皇冠会员"
          break;
        case 3:
          return "鼎级会员"
          break;      
        default:
          return "VIP会员"
          break;
      }	
    }
  }  
}	
	
